#account-list {
    padding-top: 1vw;
}

#nav-bar {
    position: fixed;
    left: 84vw;
    width: 15vw;
    height: 100vh;
    background-color: var(--text-blue);
    border-left: solid 0.2vw var(--text-black);
}

#email-info {
    position: fixed;
    top: 9vw;
    left: 29vw;
    width: 25vw;
    padding-bottom: 1vw;
    border: solid 0.2vw var(--text-black);
    border-radius: 2vw;
    background-color: var(--text-white);
}


.red-fieldset {
    border-color: var(--hover-blue);
    border-radius: 0.8vw;
    border-style: solid;
    margin: 0.5vw;
    width: 8vw;
    cursor: pointer;
}

.red-legend {
    color: var(--text-blue);
}

.transparent-input {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border: none;
    background: none;
    outline: none;
    width: 100%;
    font-size: 0.9vw;
}

.big-fieldset {
    margin: 0.7vw;
}

.big-input {
    width: 20vw;
    height: 2vw;
    font-size: 1.1vw;
}

.big-legend {
    font-size: 1.2vw;
}

#controlled-form,
#controlled-search {
    background-color: var(--text-white);
    border-radius: 2vw;
    margin: 1vw;
    padding: 0.7vw;
    border: solid 0.2vw var(--text-black);
    box-shadow: var(--shadow-blue) 0.2vw 0.4vw;
}

#add-button {
    text-align: center;
    font-size: 1.2vw;
    margin: 0.5vw;
    padding: 0.6vw;
    border-radius: 0.8vw;
    color: var(--text-blue);
    border: solid 0.15vw var(--text-black);
    cursor: pointer;
}

#add-button:hover {
    color: var(--text-white);
    background-color: var(--hover-blue);
}

#detalhes-email {
    margin: 0;
    margin-top: 1vw;
}
.td-space-before::before {
    content: "";
    margin-right: 0.5vw;
}

.td-space-after::after {
    content: "";
    margin-left: 0.5vw;
}

/******************************/

.brd-bot {
    border-bottom: solid 0.1vw var(--text-black);
}

.brd-right {
    border-right: solid 0.1vw var(--text-black);
}

.brd-round {
    border: solid 0.1vw var(--text-blue);
    border-radius: 0.5vw;
    padding: 0.3vw;
}

/******************************/

.font-info {
    font-family: sans-serif;
}

/******************************/

#account {
    margin: 1vw;
    margin-bottom: 0.2vw;
    margin-top: 0.2vw;
    cursor: pointer;
}

#account:hover {
    background-color: var(--hover-blue)
}

#account-list {
    width: 84vw;
    height: 100%;
}

#nav-button {
    height: 3vw;
    width: 10vw;
    font-family: dark, sans-serif;
    font-size: 1.2vw;
    border-radius: 1vw;
    margin: 0.5vw;
    color: var(--hover-blue);
    border: solid 0.2vw black;
    box-shadow: var(--shadow-blue) 0.2vw 0.4vw;
    background-color: var(--text-white);
    cursor: pointer;
}

#nav-button:hover {
    color: var(--text-white);
    background-color: var(--hover-blue);
}

:root {
    --text-black: black;
    --text-white: white;
    --text-blue: #0d80c2;

    --shadow-blue: #0e4e74;
    --hover-blue: #5faedb;

    --transparent: rgba(255, 255, 255, 0.9);
}

@font-face {
    font-family: light;
    src: url(/static/media/light.042dc5d4.woff);
}

@font-face {
    font-family: dark;
    src: url(/static/media/bold.c35c6c90.woff);
}

body {
    font-family: light, sans-serif;
    background-image: url(https://i.imgur.com/MOusKYj.jpg);
    background-repeat: repeat-y;
    background-size: contain;
}

.dis-flex,
.dis-flex-col,
.dis-flex-align,
.dis-flex-justify {
    display: flex;
}

.dis-flex-col {
    flex-direction: column;
}

.dis-flex-align {
    align-items: center;
}

.dis-flex-justify {
    justify-content: center;
}

/******************************/

.text-xs {
    font-size: 1vw;
}

.text-sm {
    font-size: 1.2vw;
}

.align-right {
    text-align: right;
}

.align-center {
    text-align: center;
}

/******************************/

.he-2 {
    height: 2vw;
}

/******************************/

.td-3 {
    width: 33.33%;
}

/******************************/

#app {
    width: 100%;
    min-height: 100vh;
    background-color: rgba(255, 255, 255, 0.9);
    background-color: var(--transparent);
}

